@media only screen and (min-width: 960px) {
  .js-animated {
    transition: transform 1.2s cubic-bezier(0.165, 0.84, 0.44, 1) 0s, opacity 0.8s linear 0.1s;
    transform: translate3d(0px, 80px, 0);
    opacity: 0;
  }
  .js-animated.appeared {
    transform: translate3d(0px, 0px, 0px) rotateX(0) scale(1, 1);
    opacity: 1;
  }
}

.nav-trigger {
  display: none;
  position: fixed;
  width: 36px;
  height: 36px;
  right: 0px;
  top: 0px;
  z-index: 200;
  margin: 5px 10px 5px;
  color: #ff6038;
  cursor: pointer;
}

@media only screen and (max-width: 959px) {
  .nav-trigger {
    display: block;
  }
}

.nav-trigger.is-active .nav-trigger__line {
  background-color: transparent;
}

.nav-trigger.is-active .nav-trigger__line::before {
  transform: translateY(0) rotate(45deg);
}

.nav-trigger.is-active .nav-trigger__line::after {
  transform: translateY(0) rotate(-45deg);
}

.nav-trigger__line {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: 3px;
  width: 100%;
  background-color: currentColor;
  transition: transform 0.3s, opacity 0.3s;
}

.nav-trigger__line::before, .nav-trigger__line::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: 3px;
  width: 100%;
  background-color: currentColor;
  transition: transform 0.3s, opacity 0.3s;
}

.nav-trigger__line::before {
  transform: translateY(-10px);
}

.nav-trigger__line::after {
  transform: translateY(10px);
}
