@include font('bebas-font', bebas-webfont);
@include font('helvetic-font', helveticaneue);
@include font-style('helvetic-font', helveticaneueitalic, italic);

@mixin bebas-font {
	font-family: bebas-font,sans-serif; }

@mixin helvetic-font {
	font-family: helvetic-font,sans-serif; }

body {
	overflow-x: hidden;
	background-color: #ffffff;
	background-image: url(../img/pattern-bg.png);
	background-size: 50px;
	background-position: 50%;
	@include helvetic-font;
	font-size: 12px;
	font-weight: 1.5;
	color: $text-color;
	font-weight: 400;

	@include r($screen-lg) {
		padding-top: 60px;
		background-image: none; } }

cite {
	font-style: normal; }

img {
	max-width: 100%; }

::selection {
  background: #ffcb3c; }


.container {
	max-width: 960px;
	margin: 0 auto;
	padding: 0 10px;

	@include r($screen-lg) {
		padding: 0 5px; } }

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden; }


::-webkit-scrollbar {
	width: 15px;
	height: 15px; }

::-webkit-scrollbar-thumb:vertical {
	height: 15px;
	background-color: $color--important;
	-webkit-border-radius: 10px; }

::-webkit-scrollbar-thumb:horizontal {
	width: 15px;
	background-color: $color--important;
	-webkit-border-radius: 10px; }
